                      var imgUrl = "https://p1.meituan.net/128.180/movie/" + element.img.split("/")[5] ;
                      element.img = imgUrl;
                      // console.log(element);
                    });
                  _this.message[2].img="https://p0.meituan.net/128.180/moviemachine/f7d2ad70eb79d6d9b8a197713db9b8c41711752.jpg"
                // console.log(_this.message[1])   
        });
   },
 }
</script>